* {
	@include box-sizing;
}

body {
	
}

.main {
	
}

.slider_wrapper {

	.slider {
		img {
			width: 100%;
			display: block;
		}

		.owl-item li {
				position: relative;

				&:before {
				content: '';
				position: absolute;
				top: 0; left: 0; right: 0; bottom: 0;
				z-index: 2;
				background: rgba(0, 0, 0, .5); 
			}
		}
	}

	.owl-nav div {
		width: 67px;
		height: 67px;
		position: absolute;
		top: 50%;
		margin-top: -33px;
		background: #fff;
		font-size: 0;
	}
	.owl-prev {
		left: 0;

	}
	.owl-next {
		right: 0; 
	}
}

