.header_wrapper {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 3;

	
	.header {
		@include site_width; 
		@extend %clearfix;
    	position: relative;
		padding-top: 40px;
	}
} 


.logo {
	float: left;
	width: 170px;

	img {
		width: 100%;
	}
}

.menu_icon_wrapper {
	float: right; 
}



/* Menu icon styles */

.menu_icon_wrapper {
	position: relative;
	display: inline-block;
    width: 34px;
    height: 34px;
	pointer-events: none;
	transition: 0.1s;
}

.menu_icon_wrapper.scaled {
	-webkit-transform: scale(0.5);
	-ms-transform: scale(0.5);
	transform: scale(0.5);
}

.menu_icon_wrapper svg {
	position: absolute;
	top: -33px;
	left: -33px;
	-webkit-transform: scale(0.1);
	-ms-transform: scale(0.1);
	transform: scale(0.1);
	-webkit-transform-origin: 0 0;
	-ms-transform-origin: 0 0;
	transform-origin: 0 0;
}

.menu_icon_wrapper svg path {
	stroke: #fff; 
	// height of lines
	stroke-width: 40px;

	stroke-linecap: round;
    stroke-linejoin: round;
	fill: transparent;
}

.menu_icon_wrapper .menu_icon_trigger {
	position: relative;
	width: 100%;
	height: 100%;
	cursor: pointer;
	pointer-events: auto;
	background: none;
	border: none;
	margin: 0;
	padding: 0;
}

.menu_icon_wrapper .menu_icon_trigger:hover,
.menu_icon_wrapper .menu_icon_trigger:focus {
	outline: none;
}







